* {
    margin: 0;
    padding: 0;
  }
  .NarutoKick {
    background: linear-gradient(to left, #fc0000, #fe3f3f);
    width: 100vw;
    height: 100vh;
    .container {
      background: url('./images/bg.webp') no-repeat bottom right;
      background-size: contain;
      font-family: Arial, Helvetica, sans-serif;
    }
    .loading {
        position: fixed;
        height: 100vh;
        width: 100vw;
        left: 0;
        top: 0;
        background-color: white;
      }
      .loading .content {
        height: 330px;
        width: 540px;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -165px;
        margin-left: -270px;
      }
      .loading .content .banner {
        display: inline-block;
        width: 540px;
        height: 303px;
        background: url('./images/icon.webp') no-repeat center;
        background-size: 100% 100%;
      }
      .loading .content {
        text-align: center;
        font-size: 20px;
        color: #666666;
      }
      .loading .content span {
        color: var(--themeColor);
        padding-left: 8px;
      }
      .controler {
        width: 100%;
        position: absolute;
        bottom: 15%;
        text-align: center;
        .ant-btn {
            color: white;
            border: 1px solid white;
        }
      }
      .logo {
        position: absolute;
        top: 56px;
        left: 56px;
        width: 321px;
        height: 150px;
        background: url('./images/icon.webp') no-repeat center;
        background-size: 100% 100%;
        filter: drop-shadow(0px 1px 5px rgba(255, 255, 255, .2));
      }
  }